<script>
import results from "../../data/db.json";
import LoaderComponents from "../components/LoaderComponents.vue";

export default {
  name: "Outcomes",
  // dichiaro i componenti importati
  components: {
    LoaderComponents,
  },
  props: {
    min: Number,
    max: Number,
  },
  data() {
    return {
      results: results,
      res: [],
      ////
      welcome: true,
      loading: false,
      searching: false,
      showArticles: false,
      showChapters: false,
      showConferences: false,
    };
  },
  methods: {
    searchArticles(min, max) {
      //this.results = require("../../data/db.json");
      (this.min = min),
        (this.max = max),
        (this.welcome = false),
        (this.showArticles = false),
        (this.showChapters = false),
        (this.showConferences = false),
        (this.searching = true);
        (this.loading = true);
      setTimeout(() => {
        this.res = this.results.articles.slice(min, max); // props
        this.showArticles = true;
        (this.searching = false);
        this.loading = false;
        scroll(0, 0); // top of browser page
        console.log(this.res);
      }, 500);
    },
    searchChapters(min, max) {
      //this.results = require("../../data/db.json");
      (this.min = min),
        (this.max = max),
        (this.welcome = false),
        (this.showArticles = false),
        (this.showChapters = false),
        (this.showConferences = false),
        (this.searching = true);
        (this.loading = true);
      setTimeout(() => {
        this.res = this.results.chapters.slice(min, max); // props
        this.showChapters = true;
        (this.searching = false);
        this.loading = false;
        scroll(0, 0); // top of browser page
        console.log(this.res);
      }, 500);
    },
    searchConferences(min, max) {
      //this.results = require("../../data/db.json");
      (this.min = min),
        (this.max = max),
        (this.welcome = false),
        (this.showArticles = false),
        (this.showChapters = false),
        (this.showConferences = false),
        (this.searching = true);
        (this.loading = true);
      setTimeout(() => {
        this.res = this.results.conferences.slice(min, max);
        this.showConferences = true;
        (this.searching = false);
        this.loading = false;
        scroll(0, 0);
        console.log(this.res);
      }, 500);
    },
    // funzione che legge l'id e la categoria dell'item e rediretta alla pagina di dettaglio
    goTodetail(pId, ctg) {
      this.$router.push({
        name: "Details",
        params: { id: pId, category: ctg },
      });
    },
  },
};
</script>
